import * as actionTypes from '../action/actionTypes';


const initialState={
       
    campaignOption : { 
        
        option:[ {id:'campaign1' ,name:'campaign1'},
    {id:'campaign2' ,name:'campaign2'}
         ]
},
    valueData:false,
    allData:[],
    loading:false,
    insertAllow:null,
    loadingSub:false,

    authToken:null,
    showSpinner: false,
    error:false,
    userId:null,
    message:null,
    name:null,
    notMactchErr:null

    }





 const reducer = (state = initialState,action)=>{

    switch (action.type) {
        case actionTypes.FETCH_CAMPAIGNS:

        const CampState ={...state.campaignOption};
        CampState.option=action.data;
          return {

            ...state,
            campaignOption:CampState,
            valueData:!state.valueData,
            loading:false

          }


          case actionTypes.FETCH_CAMPAIGNS_START:
    
            return {
  
              ...state,
              loading:true
  
            }
  
            case actionTypes.FETCH_CAMPAIGNS_FAIL:
    
              return {
    
                ...state,
                loading:false,
                error:true

    
              }


            case actionTypes.FETCH_ALLDATA:
    
              return {
    
                ...state,
                allData:action.allData,
                loading:false

    
              }

              case actionTypes.FETCH_ALLDATA_FAIL:
    
                return {
      
                  ...state,
                  loading:false,
                  error:true
  
      
                }


              case actionTypes.SUBMIT_DATA_START:
    
                return {
                    ...state,
                    loadingSub:true
                }

              case actionTypes.SUBMIT_DATA:
    
                return {
                    ...state,
                  insertAllow:action.insertData,
                  loadingSub:false
                }

                case actionTypes.SUBMIT_DATA_FAIL:
    
                  return {
        
                    ...state,
                    loading:false,
                    error:true
    
        
                  }


 //-----------------------------------------LOGIN AUTH-------------------------------------     


                case actionTypes.AUTH_START: 
                return {
    
                  ...state,
                  showSpinner:true
    
                };
                case actionTypes.AUTH_SUCCESS: 
                return {
    
                  ...state,
                  authToken: action.authToken,
                  showSpinner:false,
                  error: false,
                  userId:action.userId,
                  message:action.message,
                  name:action.name,
                  notMactchErr:null
    
                };
    
                case actionTypes.AUTH_FAIL: 
                return {
    
                  ...state,
                  error: true,
                  showSpinner:false,
                  message:null,
                  notMactchErr:null
    
                };
    
                case actionTypes.AUTH_LOGOUT: 
                return {
    
                  ...state,
                  authToken: null,
                  userId:null,
                  message:null,
                  insertAllow:null,
                  notMactchErr:null
    
                };

                case actionTypes.AUTH_NOT_MATCH:
                  return{
                      ...state,
                      notMactchErr:action.notMatchErr,
                      error:false,
                      showSpinner:false
                      

                  }
    
       
          default:
      
            return state;
    }





}


export default reducer;


