import React from 'react';
import classes from './Table.css';
import Button from '../Button/Button'



const table =(props)=>{
  
  
    

    let list = <h4 style={{color:'red', textAlign:'center'}}>No Records Found.</h4>;
    if(props.allData){

     list = props.allData.map((data,index)=>{  return (
                
        <tr key = {data.id}>
        <td>{index+1}</td>
    <td>{data.name}</td>
        <td>{data.enable_disable_value}</td>
        <td><Button  clicked = {()=>props.deleteHandler(data.id)} btnName = 'Delete' styleBtn = 'Delete'/></td>
      </tr>
  
    )});
    }
return(
<div className={classes.center}>

<table  >
<tbody>
<tr>
<th>Sr.No.</th>
<th>Campaign Name</th>
<th>Enable/Disable </th>
<th>Delete </th>
</tr>

{list}
</tbody>
</table>

</div>

);
     }



export default table ;


