import React, { Component } from 'react';
import {connect} from 'react-redux';

import classes from './Module.css';
import EnableFlow from '../EnableFlow/EnableFlow';
import {  Route, NavLink ,Switch} from 'react-router-dom';
import Logo from '../../components/Logo/Logo';
import Aux from '../../hoc/Axiliury/Axilury';
import Auth from '../../containers/Auth/Auth';
import Logout from '../../containers/Auth/Logout/Logout';





class Module extends Component {
    state = {

        auth: true
    };
   

    render () {

        





        return (
            <Aux>
            <div  >
                <header>
                <nav className={classes.Module}>
                 
                    {  this.props.authToken ?   <ul>
                        <li> <NavLink to="/home" 
                                exact
                                   activeStyle={{ 
                                    color: 'black',
                                    }}
                                >Home </NavLink>  </li>
                         <li> <NavLink to="/module/" 
                                exact
                                   activeStyle={{ 
                                    color: 'black',
                                    }}
                                >Enable/Disable FLow </NavLink>  </li>
                
                                <li> <NavLink
                                activeStyle={{ 
                                   color: 'black',
                                   }}
                               to={{
                                   pathname:"/logout",
                                           
                               }}>Logout</NavLink> </li>  <Logo/> </ul> : null }
                              
                              {/*<p  className={classes.Welcome}>Hi {this.props.name}</p>*/}
                   
                </nav>


                
                </header>
               { /* For render the inline blocks or tags      <Route  path="/"  exact  render={()=> <h1>Home</h1>  }  />
                <Route  path="/"    render={()=> <h1>Home 2</h1>  }  />*/}
             
            <div> <p  className={classes.Welcome}>Hi {this.props.name} </p></div>
             <Switch>
        
             <Route  path="/module/"  component={EnableFlow}    />
             <Route   path="/logout/" component={Logout}     />
             <Route   path="/home"    />
             <Route   path="/"  component={Auth} /> 
             
             {/*   Catching Error If page not found */}
            {/* <Redirect from ='/' to= '/posts'/> */}

             </Switch>
           
     
            </div>
            </Aux>
        );
    }
}
//Redirect component use to replace the url
//exact is use for compare the current path and previous path


const mapStateToProps=state=>{


    return{

        authToken: state.authToken,
        name:state.name
    }
}


export default  connect(mapStateToProps ) (Module);


