import React from 'react';
import classes from './Input.css';


let inputElement=null;
const inputClasses= [classes.InputElement];

const input =(props)=>{
    
   // console.log("HHH",props.type);
  //console.log(props.elementData);

switch (props.type) {
    case ('label'):
        inputElement= 
        <label className = {classes.Label}>{props.labelName}</label>;
        break;
        case ('textbox'):
            inputElement= <input className={classes.InputText} onChange={props.changed}  {...props.elementData} value={props.value}/>;
            break;
        case ('textarea'):
            inputElement =  <textarea className={inputClasses.join(' ')} onChange={props.changed}  {...props.elementData} value={props.value}/>;
             break; 
    
             case ('select'):
                // console.log(props.elementData.elementData.option)
            inputElement = ( <select className={inputClasses.join(' ')} onChange={props.changed}    value={props.value}> 
            
               {props.elementData.option.map(option=>{
    
                   return(
                   <option key = {option.id} value = {option.id}>{option.name}</option>
    
                   )
               })}
            
            </select>);
             break; 
        default:
            inputElement= <input className={inputClasses.join(' ')} onChange={props.changed} {...props.elementData} value={props.value}/>;
            break;
        break;

            }

return(

<div >

{inputElement}
</div>

);

}

export default input;


