import React , {Component} from 'react';
import {connect} from 'react-redux';


import classes from  './EnableFlow.css';
import {Route,Redirect} from 'react-router-dom';
import Aux from '../../hoc/Axiliury/Axilury';
import Input from '../../components/UI/Input/Input';
import Button from '../../components/UI/Button/Button';
import Table from '../../components/UI/Table/Table';
import Spinner from '../../components/UI/Spinner/Spinner';
import * as actions from '../../store/action/index';
import errImage from '../../Assets/error.png';








class Posts extends Component{

    state={
        posts: [],
        data:{
        campaign:{ 
            elementType: 'select',
        elementData:{ option:[ {id:'campaign1' ,name:'campaign1'},
                               {id:'campaign2' ,name:'campaign2'}
                                    ]
         },
         elementName:'campaign',
         value:'1',
         validity:{},
         enableValid:true
        },
        EDflow:{ 
            elementType: 'select',
        elementData:{ option:[ {id:'Enable' ,name:'Enable'},
                               {id:'Disable' ,name:'Disable'}
                           ]
         },
         elementName:'EDflow',
         value:'Enable',
         validity:{},
         enableValid:true
        },
    },
        selectIdVal: null,
        error: false,
        allData:[],
        valueData:false,


    };

componentDidMount(){ 
 
    this.props.onAllData();
    this.props.onCampaignData();

    

}





submitHandler=(event)=>{

   const submitVal = '/AppData/InsertAndDelete/insert.php?campaign_id='+this.state.data.campaign.value+'&enable_value='+this.state.data.EDflow.value;
    event.preventDefault(); 
    this.props.submitData(submitVal);
}



selectedIdHandler=(id)=>{
 
       //this.props.history.push({pathname:'/'+id}) second Way 
this.props.history.push('/posts/'+id); 
}

onChangeHandler=(event,value)=>{

const MainState = {...this.state.data};
const slectValue ={...MainState[value]};
slectValue.value = event.target.value;
MainState[value]=slectValue;
this.setState({data:MainState});


}




deleteHandler=(id)=>{

    const deleteData = '/AppData/InsertAndDelete/delete.php?id='+id;
    this.props.onDeleteData(deleteData)

    
      }



render(){
    

   
    let insertCheck = null;

    if(this.props.insertAllow === 'error'){

        insertCheck = <h4 style={{color:'red',textAlign:'center'}}>Insertion Not Allowed</h4>;
    }
    else if(this.props.insertAllow === 'Ok'){

        insertCheck = <h4 style={{color:'green',textAlign:'center'}}>Sucessfully updated </h4>;

    }
   

    
let elemetData=null;
    if(this.props.loading){

        elemetData= <div><Spinner/></div>
    }

    else{
  
        if(this.props.error){

            elemetData = <div style={{textAlign:"center"}}  ><h2>Network Issue... App Crash</h2>;
            <img  src= {errImage} alt="error"/></div> 
        }
        else{

        if(!this.props.authToken ){
           
            elemetData =  <Redirect to = "/" />;
        }
        else{

 elemetData= <div>
        {insertCheck}
   <div className={classes.mianDiv}>
<div className= {classes.EnableFlow} >
<form onSubmit={this.submitHandler}>
     <div style={{textAlign:'center'}} ><h3>Enable Disable Flow</h3>  </div> 
     <div className={classes.row}>
<div className={classes.col25}>
<Input  labelName = 'Select Campaign:'   type='label'  />
</div>
<div className={classes.col75}>
<Input changed = {(event)=>this.onChangeHandler(event,this.state.data.campaign.elementName)} elementData = {this.props.campaignOption} value={this.state.data.campaign.value}   type={this.state.data.campaign.elementType}   />
</div>
</div>
<div className={classes.row}>
<div className={classes.col25}>
 <Input  labelName = 'Enable Disable:' type='label'  />
</div>
<div className={classes.col75}>
<Input  changed = {(event)=>this.onChangeHandler(event,this.state.data.EDflow.elementName)}  elementData = {this.state.data.EDflow.elementData} value={this.state.data.EDflow.value}   type={this.state.data.EDflow.elementType}   />
</div>
</div>
<div className={classes.ButtonDiv}>
{this.props.loadingSub ? <Spinner/> :null }
<Button clicked={this.saveDataHandler}  styleBtn = 'Button' btnName = 'Save Data'/>

</div>

     </form>

</div>
</div>

<Table  deleteHandler = {this.deleteHandler} allData = {this.props.allData}   />
</div>


;
        }

    }

    }

    return(
     <Aux style={{backgroundColor: "white"}} >
{elemetData}
     </Aux>  



    );
}

}

const mapStateToProps=state=>{

    return{
        campaignOption: state.campaignOption,
        valueData: state.valueData,
        allData:state.allData,
        loading: state.loading,
        insertAllow: state.insertAllow,
        loadingSub:state.loadingSub,
        authToken: state.authToken,
        error: state.error
    
    }
}


const mapDispatchToProps=dispatch=>{

    return{

        onCampaignData: ()=>dispatch(actions.FetchCampaign()),
        onAllData: ()=>dispatch(actions.FetchData()),
        submitData: (data)=> dispatch(actions.submitData(data)),
        onDeleteData: (id)=> dispatch(actions.deleteData(id))

    }


}


export default connect(mapStateToProps,mapDispatchToProps) (Posts);


