import * as actionTypes from './actionTypes';
import Axios from '../../axios';




export const FetchCampaignStart=()=>{

return{

    type: actionTypes.FETCH_CAMPAIGNS_START

}

}

export const FetchCampaignSuccess=(data)=>{

    return{
    
        type: actionTypes.FETCH_CAMPAIGNS,
        data: data
    
    }
    
    }

 export const FetchCampaignFail=()=>{

        return{
        
            type: actionTypes.FETCH_CAMPAIGNS_FAIL
                
        }
        
        }



export const FetchCampaign=()=>{

    return dispatch=>{
        dispatch(FetchCampaignStart());
        const  key = '/AppData/CampaignFetch/campFetch.php';
        Axios.get(key).then(res=>{ 
  
          dispatch(FetchCampaignSuccess(res.data)) ;
  
}).catch( error => {

    dispatch(FetchCampaignFail());
} );

    }


    }





    export const FetchDataStart=()=>{

        return{
        
            type: actionTypes.FETCH_ALLDATA_START
        
        }
        
        }


    export const FetchDataSuccess=(data)=>{

        return{
        
            type: actionTypes.FETCH_ALLDATA,
            allData:data
        
        }
        
        }
    
     export const FetchDataFail=()=>{
    
            return{
            
                type: actionTypes.FETCH_ALLDATA_FAIL
                    
            }
            
            }
    

    export const FetchData=()=>{
        const  PullData = '/AppData/AllDataFetch/fetchData.php';
        return dispatch=>{
      dispatch(FetchDataStart())
     
   
      Axios.get(PullData).then(response=>{ 
     //  console.log("All",response.data);
          dispatch(FetchDataSuccess(response.data))
    
          }).catch( error => {
            dispatch(FetchDataFail())
          } );

        }


   }








   export const submitStart=()=>{

    return{
    
        type: actionTypes.SUBMIT_DATA_START
    
    }
    
    }


export const submitSuccess=(data)=>{

    return{
        
    
        type: actionTypes.SUBMIT_DATA,
        insertData: data
   
    
    }
    
    }

 export const submitFail=()=>{

        return{
        
            type: actionTypes.SUBMIT_DATA_FAIL
                
        }
        
        }


export const submitData=(submitVal)=>{

    return dispatch=>{
  dispatch(submitStart());
 Axios.get(submitVal).then(res=>{ 
     dispatch(submitSuccess(res.data.status ));
     dispatch(FetchData( ));
}).catch( error => {
    dispatch(submitFail());

} );


    }


}









 export const deleteFail=()=>{

        return{
        
            type: actionTypes.SUBMIT_DATA_FAIL
                
        }
        
        }


export const deleteData=(deleteData)=>{

    return dispatch=>{
  
  Axios.get(deleteData).then(res=>{ 
    dispatch(FetchData( ));    
   }).catch( error => {
   
   this.setState({error: true})
   dispatch(deleteFail( ));    
   } );


    }


}

   


